
import { AjaxHttp } from '../../../js/ajaxHttp'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import event from '../../../js/Event.js'
import { mapState } from 'vuex'

export default {
name: 'financeopinion', // id 审批ID action 1同意 2拒绝 type 审批类型
components: {
XTextarea
},
data() {
return {
opinion: '',
}
},
beforeDestroy() {
event.$off("saveDone");
},
beforeRouteLeave(to, from, next) {
next();
},
beforeRouteEnter(to, from, next) {
next();
},
computed: {
placeholder() {
if (this.$route.params.action === '1') {
return '填写确定理由（非必填）';
} else {
return '填写驳回理由（非必填）';
}
},
...mapState([
'crmDetailInfo'
]),
},
mounted() {
event.$on('saveDone', function() {
this.saveInfos();
}.bind(this));
},
methods: {
saveInfos() {
var postParams = {};
postParams.t = 'receivingorder';
postParams.id = this.$route.params.id;
postParams.is_checked = this.$route.params.action;
postParams.check_des = this.opinion;
AjaxHttp('POST', 'm=finance&a=check', postParams, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
var name = wself.$route.params.type + "@" + wself.$route.params.id;
var cacheItem = wself.crmDetailInfo[name];
cacheItem.refreshTop = true;
cacheItem.refresh = true;
wself.$store.commit('updateCrmDetailInfo', wself.crmDetailInfo);
wself.$router.go(-1);
}
});
}
}
}




